.header {
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    align-items: center;
  
    .logo-container {
      padding: 25px 0 25px 25px 25px;
      font-size: 2vw;
      font-weight: bold;
      .logo {
        height: auto;
        width: 100%;
      }
    }
  
    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;

        &:hover {
            border-bottom: 2px solid #fff;
        }

          
      }
      .search {
        -webkit-appearance: none;
        border: 1px solid #fff;
        outline: none;
        padding: 4px;
        width: 220px;
        margin-right: 12px;
        line-height: 30px;
        border-radius: 5px;
        background: transparent;
        color: #fff;
      }
    }
  }
  